@import url('https://fonts.googleapis.com/css2?family=Montserrat');

body{
    font-family: 'Montserrat', sans-serif;
    background-color: #222;
}

.main{
    background: #222;
    position: fixed;
    height: 100vh;
    width: 100vw;
    // background: radial-gradient(farthest-corner at 40px 40px, #d82f2f, #101764);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;

    .large_hole{

        width: 71.6vw;
        height: 100vh;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .upperc{
            background: #222;
            position: relative;
            width: 71.6vw;
            height: calc( 50vh - 15vw );
        }
        .holes{
            background: rgba(0,0,0,0);
            position: relative;
            height: 30vw;
            width: 71.6vw;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: row;
    
            .hole{
                position: relative;
                height: 30vw;
                width: 17.9vw;
                overflow: hidden;
        
                &:after{
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 1.45vw;
                    width: 15vw;
                    height: 30vw;
                    box-shadow: 0px 0px 0px 2000px #222;
                }
    
            }

            .semihole{
                position: relative;
                height: 30vw;
                width: 17.9vw;
                overflow: hidden;
        
                &:after{
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 1.45vw;
                    width: 15vw;
                    height: 15vw;
                    box-shadow: 0px 0px 0px 2000px #222;
                }
            }
        
        }
    }

    
    .dummy{
        position: relative;
        height: 100vh;
        width: 14.2vw;
        background: #222;
    }

    

    .link{
        text-decoration: none;
    }

    .text{
        position: absolute;
        left: 69.65vw;
        width: 15vw;
        height: 5vw;
        top: calc( 50vh + 11vw);
        color: white;
        font-size: 3.7vw;
        cursor: pointer;
        text-decoration: none;
    }

    .guide{
        color: white;
        position: absolute;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        top: calc(50vh + 20vw);
        transition: ease-in 0.5s;
    }

}

@media screen and (max-width: 700px){
    .main{
        .guide{
            font-size: 0.8rem;
            top: 75vh;
            color: white;
            z-index: 100;
        }
    }
}

